<template>
	<div class="gallary" @click="hadegallaryhade">
		<div class="wrapper">
			<swiper :options="swiperOption">
	   		 <!-- slides -->
			    <swiper-slide v-for="item in detaildata.gallaryImgs" v-if="item.length > 0">
			    	<img class="swiperimg" :src="item">

			    </swiper-slide>
			    <!-- Optional controls -->
			     <div class="swiper-pagination"  slot="pagination"></div>
	    	</swiper>
		</div>
	</div>
</template>

<script>
	export default{
		data (){
			return{
				swiperOption: {
					pagination: '.swiper-pagination',
					paginationType: 'fraction',
					loop: true,
					observer: true,
					observeParents: true,
				}
			}
		},
		props:{
			detaildata:{
				type: Object
			}
		},
		methods: {
			hadegallaryhade(){
				this.$emit('close')
			}
		}
	}
</script>

<style lang="scss">
	.swiper-container{
		overflow: visible; 
	}
	.gallary{
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: fixed;
		z-index: 99;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: #000;
		.wrapper{
			width: 100%;
			height: 0;
			padding-bottom: 100%;
			background:#fff;
			overflow: visible;
			.swiperimg{
				width: 100%;
			}
			.swiper-pagination{
				color: #fff;
				bottom:-1rem;
			}
		}
	}
</style>