<template>
<div>
	<div class="detailbanner" @click="gallaryclick()">
		<img class="bannerimg" :src="detaildata.bannerImg">
		<div class="bannerinfo">
			<div class="title">{{detaildata.sightName}}</div>
			<div class="number">
				<i class="iconfoot icon-fanhui arrowicon"></i>
				{{gallaryImgs.length}}
			</div>
		</div>	
	</div>
	<fade>
	<gallary v-show="gallary" @close="hadegallaryclick" :detaildata = 'detaildata'></gallary>
	</fade>
</div>
</template>

<script>
	import gallary from '../gallary/gallary.vue'
	import fade from '../fade/fade.vue'
	export default{
		props:{
			detaildata:{
				type: Object
			},
			gallaryImgs:{
				type: Array
			}
		},
		components:{
			gallary,
			fade
		},
		data (){
			return{
				gallary: false
			}
		},
		methods: {
			gallaryclick(){
				this.gallary = true
			},
			hadegallaryclick(){
				this.gallary = false
			}

		}
	}
</script>

<style lang="scss">
	.detailbanner{
		overflow: hidden;
		height: 0;
		padding-bottom: 55%;
		position:relative;
		.bannerimg{
			width: 100%;
		}
		.bannerinfo{
			position: absolute;
			left: 0;
			right: 0;
			bottom:0;
			line-height: .6rem;
			display: flex;
			color: #fff;
			.title{
				flex: 1;
				font-size: .32rem;
				padding: 0 .2rem;
			}
			.number{
				margin-top: .14rem;
				text-align: center;
				font-size: .24rem;
				padding:0 .4rem;
				height: .32rem;
				line-height: .32rem;
				border-radius: .2rem;
				background: rgba(0,0,0,.8);
			}
		}
	}
</style>