<template>
	<div class="detail">
		<banner :gallaryImgs= "gallaryImgs" :detaildata='detaildata'></banner>
		<vhead></vhead>
		<div class="content">
			<list :list='list'></list>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import banner from '../components/detail/banner/banner.vue'
	import vhead from '../components/detail/header/header.vue'
	import list from '../components/detail/list/list.vue'
	export default{
		data() {
			return{
				detaildata:{},
				gallaryImgs:[],
				list:[]
			}
		},
		components: {
			banner,
			vhead,
			list
		},
		methods: {
			getDetailInfo(){
				axios.get('./detail.json? id=',{
					params: {
						id: this.$route.params.id
					}
				}).then(this.handleGetData)
			},
			handleGetData(res){
				res = res.data
				if (res.ret && res.data) {
					const data = res.data
					this.detaildata =data
					this.gallaryImgs =data.gallaryImgs
					this.list =data.categoryList
				}
			}
		},
		mounted(){
			this.getDetailInfo()
		}

	}
</script>

<style lang="scss">
	.content{
		height: 50rem;
	}
</style>