<template>
	<div class="header">
		<router-link class="headabs" tag="div" to="/" v-show="showabs">
			<i class="iconfoot icon-fanhui arrowicon"></i>
		</router-link>
		<div class="headfixed" :style = "opacityStyle" v-show="!showabs">
			<router-link class="headabs" tag="div" to="/">
				<i class="iconfoot icon-fanhui arrowicon"></i>
			</router-link>
			景点详情
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return{
				showabs: true,
				opacityStyle:{
					opacity:0
				}
			}
		},
		methods: {
			handScroll(){
				const top = document.documentElement.scrollTop
				if (top > 60 ) {
					let opacity = top / 140
					opacity = opacity > 1 ? 1 : opacity
					this.opacityStyle = {
						opacity
					}
					this.showabs = false
				}else{
					this.showabs = true
				}

			}
		},
		created (){
			window.addEventListener('scroll', this.handScroll, true)
		}
	}
</script>

<style lang="scss">
	.header{
		.headabs{
			position: absolute;
			left: .2rem;
			top: 0rem;
			width: .8rem;
			height: .86rem;
			line-height: .86rem;
			text-align: center;
			border-radius: .4rem;
			background: transparent;
			z-index: 2;
		}
		.headfixed{
			position:fixed;
			top: 0;
			left: 0;
			right: 0;
			height: .86rem;
			line-height: .86rem;
			text-align: center;
			background:#00bcd4;
			color: #fff; 
			font-size: .32rem;
			z-index: 333;
		}	
	}	
</style>